import React from "react";
import styles from './users.module.css'
import userPhoto from '../../assets/images/user.png'
import {NavLink} from "react-router-dom";

let User = ({user, followingInProgress, unfollow, follow, ...props}) => {
    return (
        <div className={styles.usersWeb}>
                <span>
                    <div>
                        <NavLink to={'/profile/' + user.id}>
                        <img src={user.photos.small != null ? user.photos.small : userPhoto}
                             className={styles.userPhoto}
                             alt=""/>
                             </NavLink>
                    </div>
                    <div>
                        {/*Кнопки подписки с реквестом на API*/}
                        {user.followed
                            ? <button disabled={followingInProgress.some(id => id === user.id)}
                                      onClick={() => {
                                          unfollow(user.id);
                                      }}>Unfollow</button>
                            : <button disabled={followingInProgress.some(id => id === user.id)}
                                      onClick={() => {
                                          follow(user.id);
                                      }}>Follow</button>}
                            </div>
                            </span>
            <span className={styles.userTile}>
                            <span className={styles.userTileInfo1}>
                            <div>{user.name}</div>
                            <div>{user.status}</div>
                            </span>
                            <span className={styles.userTileInfo2}>
                            <div>{'user.location.country'}</div>
                            <div>{'user.location.city'}</div>
                            </span>
                            </span>
        </div>)
}
export default User